___CSS_LOADER_EXPORT___.push([module.id, `*, *::before, *::after {
    box-sizing: border-box;
  }
  
  * {
    margin: 0;
  }
  
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  
  input, button, textarea, select {
    font: inherit;
  }
  
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  
  #root, #__next {
    isolation: isolate;
  }

:root {
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
    --nav-font-color: rgb(47,42,66);
}

html {
    min-height: 100%;
}

body {  
    min-height: 100vh;
    display: grid;
    grid-template-columns: 1fr 4fr;
    padding: 0px;
    margin: 0px;
}

body .navbar {
    display: grid;
    grid-template-rows: 100px 1fr;
    /* background-color: blue; */
    background-color: #F6F7F7;
}

.navbar .nav-header{
    /* border-bottom: 0.5px solid #E2E2E2; */
    padding: 20px;
    font-size: 56px;
    font-weight: 600;
    color: var(--nav-font-color);
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding-left: -5px; */
    /* background-color: #F6F7F7; */
}

.navbar .nav-container {
    display: grid;
    grid-template-rows: 40px 1fr;
    /* padding: 14px; */
    padding: 15px 20px;
    gap: 10px;
}

.nav-container .project {
  min-height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 22px;
  font-weight: 600;
  letter-spacing: 0.2;
  color: var(--nav-font-color);
}


.nav-container .project-list {
    display: grid;
    grid-template-rows: repeat(auto-fill, minmax(30px, 1fr));
    gap: 10px;
    /* padding-left: 5px;  */
}

.project-list .project-card {
    font-size: 14px;
    font-weight: 300;
    background-color: #e2e4e4;
    /* border: 1px solid black; */
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
}

.project-add {
    border: 1px dashed black;
    border-radius: 5px;
    cursor: pointer;
    /* background-color: rgb(244, 244, 244); */
}

.project-add:hover {
  box-shadow:-6px 7px 15px -4px rgba(178, 178, 178, 0.50);;
}

body .taskbar {
    display: grid;
    grid-template-rows: 100px 1fr;
    box-shadow: -4px 0px 30px 0px #E2E2E2;
    /* background-color: #F6F7F7; */
    background-color: white;
    /* background-color: red; */
}

.taskbar .task-header {
    /* border-bottom: 0.5px solid #E2E2E2; */
    padding: 0 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.task-header .left-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.task-header .right-section button {
  min-width: 45px;
  min-height: 45px;
  border-radius: 5px;
  border-style: none;
  cursor: pointer;
}

.left-section .greet {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 0px;
  color: var(--nav-font-color)
}

.left-section .date {
  margin-top: 0px;
  font-size: 16px;
  font-weight: 400;
  color: rgb(84, 84, 84);
}

.taskbar .task-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: 140px;
    /* border: 1px solid black; */
    padding: 40px 80px;
    gap: 15px;
    
}

.task-container .task {
    border: 2px solid black;
    border-radius: 5px;
    display: grid;
    grid-template-rows: 10px auto;
    /* justify-content: center; */
    flex-direction: column;
}

.task .priority {
  background-color: rgb(175, 174, 174); 
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  /* min-height: 10px; */
}

.task .task-content {
  padding: 10px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.task-container #task-dialog {
  border: 1px solid black;
  border-radius: 5px;
  display: grid;
  grid-template-rows: 10px auto;
  /* justify-content: center; */
  flex-direction: column;
  max-width: 400px;
  padding: 0px;
  
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

/* #task-dialog:not([open]) {
  display: none;
} */

#task-dialog .priority {
  background-color: rgb(175, 174, 174);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

#task-dialog .task-content {
  padding: 5px;
  display: flex;
}

#task-dialog .task-content .task-text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

#task-dialog .task-text .name {
  font-size: 24px;
  line-height: 0.9;
}

.task-text .description {
  font-size: 14px;
  font-weight: 400;
}

.task-dialog .task-text .date {
  font-weight: 500;
  font-size: 16px;
}


.task-content .task-text{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 16px;
}

.task-content .task-text p{
  line-height: 1.3;
}

.task-text .name {
  font-weight: 500;
  font-size: 1em;
}

.task-text > :last-child {
  font-size: 0.9em;
}

.task-content .task-options{
  display: flex;
  align-items: flex-start;
  gap: 5px;
}


.task-content .task-options button{
  min-width: 28px;
  font-size: 15px;
}

#add-task-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border: 1px solid black;
  border-radius: 5px;
  /* min-width: 500px; */
}

#add-task-modal .task_form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form_input_name,
.form_input_description,
.form_input_date,
.form_selection_checklist {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

label {
  font-weight: 600;
}

input[type="text"],
input[type="date"],
#input_checklist {
  border-radius: 5px;
  border: 1px solid black;
  width: 200px;
}

.task_form .submit-button {
  align-self: center;
  width: 120px;
  border-radius: 5px;
}

#expandTask[open] {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border: 1px solid black;
  border-radius: 5px;

  min-height: 300px;
  max-width: 400px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* gap: 25px; */
  font-size: 16px;
  padding: 16px;
}

#expandTask .task_header {
  display: flex;
  gap: 10px;
  /* justify-content: space-around; */
  padding: 0px 4px;
}

.task_header .task_name {
  flex: 1;
  font-size: 1.2em;
  line-height: 1.4;
  font-weight: 600;
}

.task_header .task_status {
  min-width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task_status #input_status {
  min-width: 35px;
  min-height: 35px;
  border: 3px solid black;
  border-radius: 5px;
}

#expandTask .task_body {
  padding: 0px 4px;
}

#expandTask .task_footer {
  display: flex;
  justify-content: space-between;
  font-weight: 500;
  padding: 0px 4px;
}

#expandTask .task_close {
  font-weight: 300;
  border-radius: 20px;
  border: 1px solid black;
  cursor: pointer;
}


